import React from 'react';
import './mainPage.css';
function MainPage() {
    return(<React.StrictMode>
        <Header />
        <HomeImage />
        <MostPopularArticles />
        <MostRecentArticles />
        <AboutUs />
        </React.StrictMode>)
}

class Header extends React.Component {
    render() {
        return (<div className="container-fluid mobileNav">
            <nav className="navbar fixed-top navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="./index.html">
                    NYT Post</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <a className="nav-link" href="">Home <span className="sr-only">(current)</span></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#mostPopular">Popular</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#mostRecent">Recent</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#aboutUs">About Us</a>
                        </li>
                    </ul>
                </div>
            </nav>
        </div>);
    }
}


class HomeImage extends React.Component {
    render() {
        return(<div className="container-fluid homeImage col-12"> 
        <img className="img-fluid" src={require('./img/NYTlogo.jpg')}
        alt="" />
            <div className="carousel-caption" >
                 The New York Times (NYT), sometimes also known as The Times, is an American newspaper based in New York City with
                     worldwide influence and readership. Here you can find a list of most popular and recent articles, which was posted on their official site.  
                
                <p>"All the News That's Fit to Print"</p>

            </div>
        </div>); 
    }
}

class MostPopularArticles extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            'data': null
        }
    }
    componentDidMount() {
        this.LoadData();
    }
    LoadData() {
        fetch("https://api.nytimes.com/svc/mostpopular/v2/viewed/1.json?api-key=UUrrYw7RdU4mvrjHgLHzaAEGV71z36gY")
            .then(response => response.json())
            .then(result => this.setState({ 'data': result.results }, () => { console.log(this.state.data) }));
    }
    ShowArticles(num) {
        let arr = this.state.data.map(function (item, key) {
            let num =2 ;
            let meta= "media-metadata";
            console.log(item.media+"."+num+"."+meta+"."+num+"."+"url");
            return (<ArticlePost id={item.id} title={item.title} abstract={item.abstract} url={item.url}/>)
        });
        return (arr.slice(num,num+4));
    }
    render() {
        if (this.state.data != null) {
            return (<div className="container-fluid" id="mostPopular">             
            <div className="row articleRow">
            <hr />
            <h4>Most Popular Articles</h4>    
                {this.ShowArticles(0)}                   
                <button className="btn hide-me" data-toggle="collapse" data-target="#secondRow" >Show More</button>  
            </div>  
            <div className="row collapse articleRow" id="secondRow">
                {this.ShowArticles(4)}       
                <button className="btn hide-me" data-toggle="collapse" data-target=".thirdRow" aria-expanded="false" 
                aria-controls="#thirdRow #secondRowHideBttn">Show More</button>
                <button id="secondRowHideBttn" className="btn thirdRow collapse show" data-toggle="collapse" data-target="#secondRow">Hide</button>
            </div>
            <div className="row collapse articleRow thirdRow" id="thirdRow">
                {this.ShowArticles(8)}   
                <button className="btn hide-me" data-toggle="collapse" data-target=".fourthRow" aria-expaned="false" 
                aria-controls="#fourtRow #thirdRowHideBttn">Show More</button>   
               <button id="thirdRowHideBttn" className="btn fourthRow collapse show" data-toggle="collapse" data-target="#secondRow,#thirdRow">Hide All</button>       
            </div>
            <div className="row collapse articleRow fourthRow" id="fourthRow">
                {this.ShowArticles(12)}     
                <button className="btn hide-me" data-toggle="collapse" data-target=".fifthRow" aria-expanded="false"  
                aria-controls="#fifthRow #fourthRowHideBttn">Show More</button>
                <button id="fourthRowHideBttn" className="btn  fifthRow collapse show" data-toggle="collapse" data-target="#secondRow,#thirdRow,#fourthRow">Hide All</button>         
            </div>
            <div className="row collapse articleRow fifthRow" id="fifthRow">
                {this.ShowArticles(16)}          
                <button className="btn collapse show" data-toggle="collapse" data-target="#secondRow,#thirdRow,#fourthRow,#fifthRow">Hide All</button>              
            </div>
              

            </div>            
                )
        }
        else {
            return (null);
        }
    }
}

class MostRecentArticles extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            'data': null
        }
    }
    componentDidMount() {
        this.LoadData();
    }
    LoadData() {
        fetch("https://api.nytimes.com/svc/news/v3/content/all/all.json?api-key=UUrrYw7RdU4mvrjHgLHzaAEGV71z36gY")
            .then(response => response.json())
            .then(result => this.setState({ 'data': result.results }, () => { console.log(this.state.data) }));
    }
    ShowArticles(num) {
        let arr = this.state.data.map(function (item, key) {
            let num =2 ;
            let meta= "media-metadata";
            console.log(item.media+"."+num+"."+meta+"."+num+"."+"url");
            return (<ArticlePost id={item.id} title={item.title} abstract={item.abstract} url={item.url} />)
        });
        return (arr.slice(num,num+4));
    }
    render() {
        if (this.state.data != null) {
            return (<div className="container-fluid" id="mostRecent">             
            <div className="row articleRow">
            <hr />
            <h4>Recent Articles</h4>    
                {this.ShowArticles(0)}                   
                <button className="btn hide-me" data-toggle="collapse" data-target="#secondRowRecent" >Show More</button>  
            </div>  
            <div className="row collapse articleRow" id="secondRowRecent">
                {this.ShowArticles(4)}       
                <button className="btn hide-me" data-toggle="collapse" data-target=".thirdRowRecent" aria-expanded="false" 
                aria-controls="#thirdRowRecent #secondRowHideBttnRecent">Show More</button>
                <button id="secondRowHideBttnRecent" className="btn thirdRowRecent collapse show" data-toggle="collapse" data-target="#secondRowRecent">Hide</button>
            </div>
            <div className="row collapse articleRow thirdRowRecent" id="thirdRowRecent">
                {this.ShowArticles(8)}   
                <button className="btn hide-me" data-toggle="collapse" data-target=".fourthRowRecent" aria-expaned="false" 
                aria-controls="#fourtRowRecent #thirdRowHideBttnRecent">Show More</button>   
               <button id="thirdRowHideBttnRecent" className="btn fourthRowRecent collapse show" data-toggle="collapse" data-target="#secondRowRecent,#thirdRowRecent">Hide All</button>       
            </div>
            <div className="row collapse articleRow fourthRowRecent" id="fourthRowRecent">
                {this.ShowArticles(12)}     
                <button className="btn hide-me" data-toggle="collapse" data-target=".fifthRowRecent" aria-expanded="false"  
                aria-controls="#fifthRowRecent #fourthRowHideBttnRecent">Show More</button>
                <button id="fourthRowHideBttnRecent" className="btn  fifthRowRecent collapse show" data-toggle="collapse" data-target="#secondRowRecent,#thirdRowRecent,#fourthRowRecent">Hide All</button>         
            </div>
            <div className="row collapse articleRow fifthRowRecent" id="fifthRowRecent">
                {this.ShowArticles(16)}          
                <button className="btn collapse show" data-toggle="collapse" data-target="#secondRowRecent,#thirdRowRecent,#fourthRowRecent,#fifthRowRecent">Hide All</button>              
            </div>
              

            </div>            
                )
        }
        else {
            return (null);
        }
    }
}

class ArticlePost extends React.Component {
    constructor(props) {
        super(props); 
    }
    render() {
        return (<div className="card col-3" style={{"max-width": 25+"rem"}}>            
            <div className="card-body">
                <h5 className="card-title">{this.props.title}</h5>
                <p className="card-text">{this.props.abstract}</p>             
                <a href={this.props.url} class="btn btn-primary">Open</a>
            </div>
        </div>
        )
    }
}

class AboutUs extends React.Component {
    render() {
        return(<div className="container-fluid" id="aboutUs">
            <hr />
            <h4>About Us</h4>
            <p>This site was created for entertainment and educational purposes. 
                All displayed articles are taken from the New York Times web-site using api for developers.</p>
            <h6>Author: Oleksandr Aleksyk </h6>
           <a href="https://www.instagram.com/oleksandr_aleksyk/?hl=uk"> <img src={require("./img/inst.png")} alt="Inst Logo" /></a> 
           <a href="https://www.linkedin.com/in/oleksandr-aleksyk-bbab68193/"> <img src={require("./img/linkedin.png")} alt="Link Logo" /></a>
        </div>);
    }
}


export default MainPage;